const getSizes = () => {
  // viewport size
  document.querySelector("#viewportSize").innerText = `${innerWidth}px * ${innerHeight}px`;

  // screen height
  const screenHeight = screen.height * devicePixelRatio;
  const screenWidth = screen.width * devicePixelRatio;
  document.querySelector("#screenSize").innerText = `${screenWidth}px * ${screenHeight}px`;
};

getSizes();
window.onresize = getSizes;
